@media (min-width: 768px) {
  .page-main__groop-block-wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.page-main__groop-label-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (max-width: 767px) {
    justify-content: flex-start;
  }
}

.page-main__groop-label-wrapper--impression {
  @media (max-width: 1199px) {
    max-width: 560px;
    justify-content: flex-start;
  }
}

@media (min-width: 768px) {
  .page-main__groop-line {
    position: relative;
    width: 39%;
  }

  .page-main__groop-line::before {
    position: absolute;
    content: "";
    width: 50%;
    height: 1px;
    top: 50%;
    left: 115%;
    background-color: #dbdbdb;
  }

  .page-main__groop-line::after {
    position: absolute;
    content: "";
    width: 50%;
    height: 1px;
    top: 50%;
    right: 115%;
    background-color: #dbdbdb;
  }
}

.page-main__groop-content {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
  padding: 24px 20px 39px;
  box-sizing: border-box;

  @media (min-width: 768px) {
    border-bottom: none;
    padding: 24px 60px 39px;
  }
}

.page-main__groop-content--full-name,
.page-main__groop-content--contact-info {
  @media (min-width: 768px) {
    width: 45%;
  }
}

.page-main__groop-content--full-name {
  @media (min-width: 768px) {
    border-right: 1px solid #dbdbdb;
  }
}

.page-main__groop-title {
  width: 240px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  margin: 24px auto;
}

.page-main__groop-text,
.page-main__groop-text-info {
  display: block;
  width: 240px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
  margin-left: 16px;
}

.page-main__groop-text-impression,
.page-main__groop-text-visited {
  width: 187px;
  padding-left: 55px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}

.page-main__groop-control,
.page-main__groop-control-info {
  width: 100%;
}

.page-main__groop-input {
  width: 100%;
  padding: 16px;
  box-sizing: border-box;
  background: #f2f2f2;
  color: #000000;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  border: none;

  @media (min-width: 1200px) {
    width: 430px;
  }
}

.page-main__groop-input-info-phone {
  width: 100%;
  padding: 16px 73px 16px 16px;
  box-sizing: border-box;
  color: #000000;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  background: #f2f2f2 url("../img/phone.svg") no-repeat right;
  border: none;
}

.page-main__groop-input-info-phone::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.page-main__groop-input-info-email {
  width: 100%;
  padding: 16px 73px 16px 16px;
  box-sizing: border-box;
  color: #000000;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  background: #f2f2f2 url("../img/email.svg") no-repeat center right;
  border: none;
}

.page-main__groop-input-info-email::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

//radio  

.page-main__groop-input-impression {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.page-main__groop-control-impression {
  display: flex;
  align-items: center;
  margin-bottom: 32px;
  width: 280px;
}

.page-main__groop-mark-impression {
  position: absolute;
  width: 46px;
  height: 46px;
  background: #f2f2f2;
  border-radius: 50%;
}

.page-main__groop-input-impression:checked + .page-main__groop-mark-impression {
  background-image: url("../img/radio-click.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.page-main__groop-input-impression:focus + .page-main__groop-mark-impression {
  background: #e5e5e5;
}

.page-main__groop-input-impression:checked:focus + .page-main__groop-mark-impression {
  background: #e5e5e5;
  background-image: url("../img/radio-click.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.page-main__groop-input-impression:disabled + .page-main__groop-mark-impression {
  background: #d5d5d5;
}

.page-main__groop-input-impression:checked:disabled + .page-main__groop-mark-impression {
  background: #d5d5d5;
  background-image: url("../img/radio-click-off.svg");
  background-repeat: no-repeat;
  background-position: center;
}

//radio  
//checkbox 

.page-main__groop-input-visited {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.page-main__groop-control-visited {
  display: flex;
  align-items: center;
  width: 245px;
  margin-bottom: 32px;
}

.page-main__groop-mark-visited {
  position: absolute;
  width: 46px;
  height: 46px;
  background: #f2f2f2;
  border-radius: 4px;
}

.page-main__groop-input-visited:checked + .page-main__groop-mark-visited {
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.page-main__groop-input-visited:focus + .page-main__groop-mark-visited {
  background: #e5e5e5;
}

.page-main__groop-input-visited:checked:focus + .page-main__groop-mark-visited {
  background: #e5e5e5;
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.page-main__groop-input-visited:disabled + .page-main__groop-mark-visited {
  background: #d5d5d5;
}

.page-main__groop-input-visited:checked:disabled + .page-main__groop-mark-visited {
  background: #d5d5d5;
  background-image: url("../img/tick-disabled.svg");
  background-repeat: no-repeat;
  background-position: center;
}

//checkbox 

.page-main__groop-control-emotions {
  width: 100%;
  margin-bottom: 56px;
  text-align: center;
}

.page-main__groop-textarea-emotions {
  width: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  box-sizing: border-box;
  padding: 16px;
  background: #f2f2f2;
  color: #000000;
  resize: vertical;
  border: none;

  @media (min-width: 768px) {
    min-height: 160px;
    max-width: 768px;
  }
}

@media (min-width: 768px) {
  .page-main__groop-text-emotions-block-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
    max-width: 768px;
  }
}

.page-main__groop-submit-emotions {
  text-decoration: none;
  background: #81b3d2;
  border: none;
  color: #ffffff;
  box-sizing: border-box;
  padding: 18px 34px 13px 32px;
  width: 281px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  margin-bottom: 3px;
}

.page-main__groop-text-emotions {
  text-align: end;
  display: block;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 0.6);
}
